<template>
  <div>
    <h1 class="text-h4 mb-4">Последние посты</h1>
    
    <v-row>
      <v-col v-for="post in posts" :key="post.id" cols="12" md="6" lg="4">
        <v-card>
          <v-card-title>{{ post.title }}</v-card-title>
          <v-card-text>
            <p class="text-truncate">{{ post.content }}</p>
          </v-card-text>
          <v-card-actions>
            <v-btn text color="primary" :to="{ name: 'PostDetail', params: { id: post.id }}">
              Читать далее
            </v-btn>
          </v-card-actions>
          <v-card-subtitle>
            Опубликовано: {{ formatDate(post.published_date) }}
          </v-card-subtitle>
        </v-card>
      </v-col>
    </v-row>

    <v-row v-if="posts.length === 0" class="mt-4">
      <v-col cols="12">
        <v-alert type="info">
          Пока нет опубликованных постов.
        </v-alert>
      </v-col>
    </v-row>

    <v-row v-if="totalPages > 1" class="mt-4">
      <v-col cols="12" class="text-center">
        <v-pagination
          v-model="currentPage"
          :length="totalPages"
          @input="onPageChange"
        ></v-pagination>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'PostList',
  data() {
    return {
      posts: [],
      currentPage: 1,
      totalPages: 1
    }
  },
  methods: {
    async fetchPosts() {
      try {
        const response = await axios.get(`/api/posts/?page=${this.currentPage}`)
        this.posts = response.data.results
        this.totalPages = Math.ceil(response.data.count / 10)
      } catch (error) {
        console.error('Error fetching posts:', error)
      }
    },
    formatDate(date) {
      return new Date(date).toLocaleString('ru-RU')
    },
    onPageChange(page) {
      this.currentPage = page
      this.fetchPosts()
    }
  },
  created() {
    this.fetchPosts()
  }
}
</script> 