<template>
  <div>
    <v-btn text color="primary" to="/" class="mb-4">
      <v-icon left>mdi-arrow-left</v-icon>
      Назад к списку
    </v-btn>

    <v-card v-if="post">
      <v-card-title class="text-h4">{{ post.title }}</v-card-title>
      <v-card-subtitle>
        Опубликовано: {{ formatDate(post.published_date) }}
      </v-card-subtitle>
      <v-card-text class="text-body-1">
        {{ post.content }}
      </v-card-text>
    </v-card>

    <v-progress-circular
      v-else
      indeterminate
      color="primary"
    ></v-progress-circular>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'PostDetail',
  props: {
    id: {
      type: [String, Number],
      required: true
    }
  },
  data() {
    return {
      post: null
    }
  },
  methods: {
    async fetchPost() {
      try {
        const response = await axios.get(`/api/posts/${this.id}/`)
        this.post = response.data
      } catch (error) {
        console.error('Error fetching post:', error)
      }
    },
    formatDate(date) {
      return new Date(date).toLocaleString('ru-RU')
    }
  },
  created() {
    this.fetchPost()
  }
}
</script> 